  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'Exo-light';
  src: url("../../fonts/Exo/Exo-Light.woff2") format("woff2"), url("../../fonts/Exo/Exo-Light.woff") format("woff");
