/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #ffffff;
  font-family: "Exo-medium";
  font-size: 1rem;
  line-height: 1.5; }

/* Headings */
h1, h2, h3, h4, h5, h6 {
  clear: both;
  font-weight: 100; }

/* Copy */
p {
  margin-bottom: 1.5rem; }

dfn, cite, em, i {
  font-style: italic; }

blockquote {
  margin: 0 1.5rem; }

address {
  margin: 0 0 1.5rem; }

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: .9375rem;
  line-height: 1.6;
  margin-bottom: 1.6rem;
  max-width: 100%;
  overflow: auto;
  padding: 1.6rem; }

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: .9375rem; }

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help; }

mark, ins {
  background: #fff9c0;
  text-decoration: none; }

big {
  font-size: 125%; }

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
  font-size: 1rem; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background: #222222;
  /* Fallback for when there is no custom background color defined. */ }

blockquote,
q {
  quotes: '' ''; }
  blockquote:before, blockquote:after,
  q:before,
  q:after {
    content: ''; }

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5rem; }

ul,
ol {
  margin: 0 0 1.5rem 3rem; }

ul {
  list-style: disc; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5rem; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5rem 1.5rem; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

table {
  margin: 0 0 1.5rem;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 1px solid;
  border-color: #00cc00;
  border-radius: 2px;
  background: none;
  margin-top: 20px;
  color: #00cc00;
  font-size: 0.75rem;
  line-height: 1;
  padding: 0.6rem 1rem 0.4rem; }
  button:hover,
  input[type="button"]:hover,
  input[type="reset"]:hover,
  input[type="submit"]:hover {
    border: 1px solid;
    border-color: #00cc00;
    border-radius: 2px;
    background: #00cc00;
    color: #ffffff; }
  button:active, button:focus,
  input[type="button"]:active,
  input[type="button"]:focus,
  input[type="reset"]:active,
  input[type="reset"]:focus,
  input[type="submit"]:active,
  input[type="submit"]:focus {
    border: 1px solid;
    border-color: #00cc00;
    outline: 0; }

/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  border: 1px solid;
  border-color: #00cc00;
  border-radius: 2px;
  background: none;
  margin-top: 20px;
  color: #00cc00;
  font-size: 1rem;
  line-height: 1;
  padding: 0.6rem 1rem 0.4rem;
  display: block; }
  input[type="text"]:focus,
  input[type="email"]:focus,
  input[type="url"]:focus,
  input[type="password"]:focus,
  input[type="search"]:focus,
  textarea:focus {
    color: #00cc00;
    outline: 0; }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
  padding: 3px; }

textarea {
  padding-left: 3px;
  width: 100%; }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
a {
  color: #00cc00;
  text-decoration: none; }
  a:visited {
    color: #00cc00; }
  a:hover, a:focus, a:active {
    color: #08ff08; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/* Menus */
.main-navigation {
  clear: both;
  display: block;
  float: left;
  width: 100%; }
  .main-navigation ul {
    display: none;
    list-style: none;
    margin: 0;
    padding-left: 0; }
    .main-navigation ul ul {
      box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
      float: left;
      position: absolute;
      top: 1.5rem;
      left: -999rem;
      z-index: 99999; }
      .main-navigation ul ul ul {
        left: -999rem;
        top: 0; }
      .main-navigation ul ul li:hover > ul,
      .main-navigation ul ul li.focus > ul {
        left: 100%; }
      .main-navigation ul ul a {
        width: 200px; }
    .main-navigation ul li:hover > ul,
    .main-navigation ul li.focus > ul {
      left: auto; }
  .main-navigation li {
    float: left;
    position: relative; }
  .main-navigation a {
    display: block;
    text-decoration: none; }

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
  display: block; }

@media screen and (min-width: 768px) {
  .menu-toggle {
    display: none; }
  .main-navigation ul {
    display: block; } }

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5rem;
  overflow: hidden; }

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: left;
  width: 50%; }

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  float: right;
  text-align: right;
  width: 50%; }

.menu .page_item {
  margin-right: 20px;
  padding-right: 20px;
  border-right: 1px solid white; }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: .875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
  display: table; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/* Posts and pages */
.sticky {
  display: block; }

.hentry {
  margin: 0 0 1.5rem; }

.updated:not(.published) {
  display: none; }

.single .byline,
.group-blog .byline {
  display: inline; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5rem 0 0; }

.page-links {
  clear: both;
  margin: 0 0 1.5rem; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
/* All Archives Page */
/* Submit a Quote Page */
.hfeed {
  position: relative;
  width: 1080px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2%; }
  .hfeed .site-content .content-area {
    display: grid;
    grid-template-columns: 10% 1fr 10%; }
    .hfeed .site-content .content-area .quote-left {
      padding: 0;
      grid-column: 1/2;
      font-size: 5rem;
      color: #00cc00;
      justify-self: left; }
    .hfeed .site-content .content-area .quote-right {
      grid-column: 3/4;
      padding: 0;
      font-size: 5rem;
      color: #00cc00;
      justify-self: right; }

.site-footer {
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  bottom: 20px;
  width: 1080px;
  display: grid;
  justify-content: right; }
  .site-footer .site-info {
    display: grid;
    grid-template-columns: 3fr 2fr; }
    .site-footer .site-info .main-navigation, .site-footer .site-info .broughtToYou {
      justify-self: right; }

.home .site-main {
  display: grid;
  grid-template-rows: 1fr 70px;
  justify-content: center; }
  .home .site-main .postContent {
    font-family: "Exo-light";
    font-weight: 200; }
  .home .site-main .postAuthor,
  .home .site-main .postCategories {
    display: inline; }
  .home .site-main .postCategories a {
    font-family: 'Exo-italic-normal'; }
  .home .site-main .thepost {
    display: grid;
    grid-template-rows: 1fr 50px; }
  .home .site-main .my_btn {
    width: 150px;
    justify-self: center;
    grid-row: 2/3; }

.site-header {
  display: grid;
  justify-content: center; }
  .site-header .link-logo {
    justify-self: center; }

.page-id-215 label {
  display: block;
  margin-top: 20px; }

.page-id-215 input[type="text"] {
  width: 45%; }

.page-id-215 textarea {
  height: 100px; }

.archive .page-header {
  border-bottom: 1px dashed #ffffff; }

.archive .post {
  display: grid;
  grid-template-rows: 2fr 1fr;
  border-bottom: 1px dashed #ffffff; }
  .archive .post .entry-content {
    grid-row: 1/2;
    color: white;
    font-size: 1.5rem; }
  .archive .post .entry-header {
    grid-row: 2/3; }
    .archive .post .entry-header .entry-title {
      margin-top: 0;
      margin-bottom: 0; }
    .archive .post .entry-header .entry-title a {
      color: white;
      font-size: 1rem; }
      .archive .post .entry-header .entry-title a:hover {
        color: white; }

.page-id-213 .authorItem,
.page-id-213 .tagItem,
.page-id-213 .catgItem {
  margin-right: 20px; }

.error404 .search-field {
  display: inline-block;
  font-size: 1.5rem; }

.error404 .search-submit {
  display: inline;
  border: none;
  font-size: 1.2rem; }
  .error404 .search-submit:hover {
    color: #08ff08;
    background-color: #222222; }

.wp-pagenavi {
  clear: both; }

.wp-pagenavi a, .wp-pagenavi span {
  text-decoration: none;
  padding: 3px 5px;
  margin: 2px; }

.wp-pagenavi span.current {
  font-weight: bold; }

.search-results .page-title {
  border-bottom: 1px solid dashed #ffffff; }
